export class Login extends Component {
    constructor(){
        super();
        this.state = {signin:'login'}
    }
    signin=()=>{
        this.setState({signin:'signin'});
    }
    login=()=>{
        this.setState({signin:'login'})
    }
    render() {
        return(
            <div>
                <Row className="d-flex justify-content-center align-items-center"
                     style={{height:'100vh',backgroundColor:'#f2f3f5'}}>
                    <Col xs={24} md={12} lg={6} className="bg-white">
                        {this.state.signin === 'login'&&
                        <div className="w-100 d-flex flex-column"
                             style={{height:300,padding:25}}>
                            <img className="w-25" style={{height:30}} src={logo1} alt=""/>
                            <h3 className="mt-2">ورود به حساب کاربری</h3>
                            <h4>برای استفاده از خدمات ابتدا وارد شوید</h4>
                            <input className="login mt-2" type="text" placeholder="نام کاربری"/>
                            <input className="login mt-3" type="text" placeholder="پسورد"/>
                            <div className="d-flex justify-content-between mt-4">
                                <Col lg={12} className="d-flex">
                                    <h4>عضو نیستید؟</h4>
                                    <h4 className="c-pointer" onClick={this.signin}>ثبت نام</h4>
                                </Col>
                                <button>وارد شوید</button>
                            </div>
                        </div>
                        }
                        {this.state.signin ==='signin'&&
                        <div className="w-100 d-flex flex-column"
                             style={{height:400,padding:25}}>
                            <img className="w-25" style={{height:30}} src={logo1} alt=""/>
                            <h3 className="mt-2">ثبت نام</h3>
                            <input className="login mt-2" type="text" placeholder="نام"/>
                            <input className="login mt-3" type="text" placeholder="نام خانوادگی"/>
                            <input className="login mt-3" type="text" placeholder="نام کاربری"/>
                            <input className="login mt-3" type="password" placeholder="پسورد"/>
                            <input className="login mt-3" type="password" placeholder="تکرار پسورد"/>
                            <div className="d-flex justify-content-between mt-4">
                                <Col lg={16} className="d-flex">
                                    <h4>عضو زرین‌پال هستید ؟</h4>
                                    <h4 className="c-pointer" onClick={this.login}>وارد شوید</h4>
                                </Col>
                                <button>ثبت نام</button>
                            </div>
                        </div>
                        }
                    </Col>
                </Row>
            </div>
        )
    }
}