import React, {Component} from 'react';
import {Row , Col } from 'antd';
export class Wallet extends Component {
    render() {
        const {sms} = this.props;
        return (
                <Col xs={24} md={12} lg={7} className="bg-white mr-3 mt-5">
                    <Row>
                        <Col className="d-flex justify-content-between align-items-center"
                             style={{height:50,backgroundColor:'#f8f9fa'}}>
                            {sms ?
                                <h4 className="mr-4">پیامک</h4>:
                                <h4 className="mr-4">اصلی زرین پال</h4>
                            }
                            {sms ? <h4 className="ml-4">ZP.661018.99</h4> :
                                <h4 className="ml-4">zp.661018.1</h4>
                            }
                        </Col>
                    </Row>
                    <Row>
                        <Col className="d-flex flex-column justify-content-center align-items-center"
                             style={{height:100}}>
                            <h4>موجودی</h4>
                            <div className="d-flex">
                                <h4 className="ml-3">0</h4>
                                <h4>تومان</h4>
                            </div>
                        </Col>
                    </Row>
                    <Row className="d-flex" style={{backgroundColor:'#f8f9fa',height:65}}>
                        <Col xs={8} md={8} lg={8}
                             className="d-flex top-border flex-column justify-content-center align-items-center">
                            <h5>ورودی</h5>
                            <h4>0</h4>
                        </Col>
                        <Col xs={8} md={8} lg={8}
                             className="d-flex flex-column bottom justify-content-center align-items-center">
                            <h5>خروجی</h5>
                            <h4>0</h4>
                        </Col>
                        <Col xs={8} md={8} lg={8}
                             className="d-flex top-border flex-column justify-content-center align-items-center">
                            <h5>در حال خروج</h5>
                            <h4>0</h4>
                        </Col>
                    </Row>
                </Col>
        );
    }
}

