import React, {Component} from 'react';
import { Menu, Icon, Button,Avatar } from 'antd';
import profile from '../../image/profile.jpg';

const SubMenu = Menu.SubMenu;

export class SideBar extends Component {
    constructor(props){
        super(props);
    }


    render() {
        const {main} = this.props;
        return (
            <div style={{ width: '100%' }}>
                <div className={this.props.menu==false?
                    "avatar d-flex flex-column justify-content-center align-items-center bg-gradient":
                    "avatar d-flex flex-column justify-content-center"}>
                    <Avatar className="mt-4" size={75} src={profile} />
                    {this.props.menu==false&&
                        <React.Fragment>
                    <h3 className="mt-3 t-white">مهدی تاریوردی زاده</h3>
                    <h4 className="t-white mb-4">سطح عضویت : جدید</h4>
                        </React.Fragment>}
                </div>
                {this.props.menu==false &&
                <div className="d-flex justify-content-center my-3">
                    <h4 className="level">ارتقاء به سطح نقره ای</h4>
                </div>}
                <Menu
                    defaultSelectedKeys={['1']}
                    defaultOpenKeys={['sub1']}
                    mode="inline"
                    inlineCollapsed={this.props.menu}
                >
                    <Menu.Item key="1" onClick={main} value="wallet">
                        <Icon type="wallet" />
                        <span className="mr-2">کیف پول</span>
                    </Menu.Item>
                    <Menu.Item key="2" onClick={main} value="port">
                        <Icon type="chrome" />
                        <span className="mr-2">درگاه های پرداخت</span>
                    </Menu.Item>
                    <Menu.Item key="3" onClick={main} value="edit">
                        <Icon type="edit" />
                        <span className="mr-2">ویرایش اطلاعات</span>
                    </Menu.Item>
                    {this.props.isAdmin &&
                    <Menu.Item key="4" onClick={main} value="AllBuys">
                        <Icon type="shopping" />
                        <span className="mr-2">تمام خریدها</span>
                    </Menu.Item>
                    }
                </Menu>
            </div>
        );
    }
}
